
.footer {
    flex: 0 0 auto;
    width: 100%;
    height: auto;
    display: flex;
    align-items: center;
    justify-content: center;
    background-color: var(--dl-color-gray-black);
}

.footer-container {
    width: 100%;
    display: flex;
    max-width: var(--dl-size-size-maxwidth);
    align-items: center;
    flex-direction: column;
    padding: var(--dl-space-space-fourunits) var(--dl-space-space-threeunits);
    justify-content: space-between;
}

.footer-links {
    gap: var(--dl-space-space-oneandhalfunits);
    display: flex;
    max-width: var(--dl-size-size-maxwidth);
    align-items: center;
    flex-direction: column;
    justify-content: flex-start;
}

.footer-logo {
    color: var(--dl-color-gray-white);
    font-size: 2em;
    font-weight: bold;
}

.footer-nav {
    flex: 0 0 auto;
    display: flex;
    margin-top: 0;
    align-items: center;
    justify-content: space-between;
    flex-direction: row;
    color: var(--dl-color-secondary-gray500);
}

.footer-nav > span:not(:last-child) {
    margin-right: 10px;
}

.footer-separator {
    width: 100%;
    border: 1px solid var(--dl-color-gray-800);
    margin: var(--dl-space-space-twounits) 0;
}

.footer-text {
    flex: 0 0 auto;
    width: 100%;
    display: flex;
    align-items: center;
    flex-direction: row;
    justify-content: space-between;
    color: var(--dl-color-secondary-gray500);
}

.footer-icons {
    display: flex;
    align-items: center;
    flex-direction: row;
    justify-content: space-between;
}