.header {
    width: 100%;
    display: flex;
    align-items: center;
    padding-top: var(--dl-space-space-oneandhalfunits);
    flex-direction: column;
    padding-bottom: var(--dl-space-space-oneandhalfunits);
    background-color: var(--dl-color-primary1-blue80);
}

.header-container {
    width: 90%;
    display: flex;
    position: sticky;
    max-width: var(--dl-size-size-maxwidth);
    align-items: center;
    border-radius: 50px;
    padding: var(--dl-space-space-oneandhalfunits) var(--dl-space-space-threeunits);
    justify-content: space-between;
    background-color: var(--dl-color-gray-white);
}

.header-logo {
    font-size: 2em;
    font-family: "Lato", serif;
    font-weight: bold;
    text-transform: uppercase;
    text-decoration: none;
    color: black;
}

.header-desktop-menu {
    flex: 1;
    display: flex;
    justify-content: space-between;
}

.header-links {
    flex: 1;
    display: flex;
    align-items: center;
    flex-direction: row;
    justify-content: center;
}

.header-links > a:not(:last-child) {
    margin-right: 50px;
}

.header-link {
    text-decoration: none;
    color: black;
    transition: 0.3s;
}

.header-link:hover {
    color: #3d6e70ff;
}

.header-buttons {
    display: flex;
    align-items: center;
    flex-direction: row;
    justify-content: space-between;
}

.header-login {
    border-color: var(--dl-color-primary1-blue100);
    border-width: 1px;
    margin-right: var(--dl-space-space-twounits);
    padding-left: 1.5rem;
    border-radius: 45px;
    padding-right: 1.5rem;
}

.header-register {
    color: var(--dl-color-gray-white);
    transition: 0.3s;
    font-weight: bold;
    border-color: var(--dl-color-primary1-blue100);
    padding-left: 1.5rem;
    border-radius: 45px;
    padding-right: 1.5rem;
    background-color: var(--dl-color-primary1-blue100);
}

.home-register:hover {
    border-color: rgba(41, 20, 119, 0.9);
    padding-left: 1.5rem;
    padding-right: 1.5rem;
    background-color: rgba(41, 20, 119, 0.9);
}