.home-hero {
    flex: 0 0 auto;
    width: 100%;
    height: auto;
    display: flex;
    align-items: flex-start;
    justify-content: center;
    background-color: var(--dl-color-primary1-blue80);
}

.home-hero-container {
    max-width: var(--dl-size-size-maxwidth);
    min-height: auto;
    flex-direction: column;
    padding: var(--dl-space-space-sixunits) var(--dl-space-space-threeunits);
}

.home-hero-header {
    gap: var(--dl-space-space-twounits);
    display: flex;
    align-items: center;
    flex-direction: column;
    justify-content: flex-start;
}

.home-hero-heading {
    color: var(--dl-color-gray-white);
    max-width: 800px;
    text-align: center;
    font-family: "Raleway", serif, serif;
    line-height: 1.6;
}

.home-hero-sub-heading {
    color: var(--dl-color-gray-white);
    font-size: 18px;
    text-align: center;
    line-height: 1.6;
}

.home-btn-group {
    gap: var(--dl-space-space-oneandhalfunits);
    display: flex;
    align-items: center;
    flex-direction: row;
}

.home-hero-button1 {
    color: var(--dl-color-gray-white);
    font-size: 18px;
    transition: 0.3s;
    font-weight: bold;
    border-color: var(--dl-color-primary1-blue100);
    border-width: 0;
    border-radius: 45px;
    padding: 1.5rem 3rem;
    background-color: var(--dl-color-primary1-blue100);
}

.home-hero-button1:hover {
    color: var(--dl-color-gray-white);
    border-color: rgba(41, 20, 119, 0.9);
    background-color: rgba(41, 20, 119, 0.9);
}

.home-hero-button2 {
    color: var(--dl-color-gray-white);
    transition: 0.3s;
    font-weight: bold;
    border-color: transparent;
    padding: var(--dl-space-space-unit) var(--dl-space-space-twounits);
    background-color: transparent;
}

.home-hero-button2:hover {
    border-color: var(--dl-color-gray-white);
}

.home-details {
    flex: 0 0 auto;
    width: 100%;
    height: auto;
    display: flex;
    align-items: center;
    justify-content: center;
}

.home-details-container {
    width: 100%;
    display: flex;
    max-width: var(--dl-size-size-maxwidth);
    align-items: center;
    padding: var(--dl-space-space-fourunits) var(--dl-space-space-threeunits);
    justify-content: space-between;
}

.home-details-sub-heading {
    color: var(--dl-color-gray-800);
    font-size: 18px;
    text-align: left;
    line-height: 1.6;
    margin-bottom: var(--dl-space-space-twounits);
}

.home-details-image {
    width: 400px;
    height: 400px;
    object-fit: cover;
    margin-left: var(--dl-space-space-fourunits);
    border-radius: var(--dl-radius-radius-round);
}

.home-features {
    flex: 0 0 auto;
    width: 100%;
    height: auto;
    display: flex;
    align-items: center;
    justify-content: center;
    background-color: var(--dl-color-secondary-grey400);
}

.home-features-container  {
    gap: var(--dl-space-space-threeunits);
    width: 100%;
    display: flex;
    max-width: var(--dl-size-size-maxwidth);
    align-items: center;
    flex-direction: column;
    padding: var(--dl-space-space-fourunits) var(--dl-space-space-threeunits);
    justify-content: flex-start;
}

.home-features-header {
    display: flex;
    align-items: center;
    flex-direction: column;
    justify-content: flex-start;
}

.home-features-body {
    width: 100%;
    display: grid;
    grid-gap: var(--dl-space-space-twounits);
    grid-template-columns: 1fr 1fr;
}

.home-pricing {
    flex: 0 0 auto;
    width: 100%;
    height: auto;
    display: flex;
    align-items: center;
    justify-content: center;
}

.home-pricing-container  {
    gap: var(--dl-space-space-twounits);
    width: 100%;
    display: flex;
    align-items: center;
    flex-direction: column;
    padding: var(--dl-space-space-fourunits) var(--dl-space-space-threeunits);
    justify-content: center;
    background-color: var(--dl-color-gray-white);
}

.home-pricing-header {
    gap: 0;
    display: flex;
    align-items: center;
    flex-direction: column;
    justify-content: flex-start;
}

.sectionTitle {
    color: var(--dl-color-primary1-blue80);
    font-family: "Raleway", serif;
    margin-bottom: var(--dl-space-space-oneandhalfunits);
}

.sectionHeading {
    font-family: "Raleway", serif;
    line-height: 1.6;
    margin-bottom: var(--dl-space-space-oneandhalfunits);
}

.subHeading {
    color: var(--dl-color-gray-800);
    font-size: 18px;
    text-align: center;
    line-height: 1.6;
}

.home-pricing-body{
    gap: var(--dl-space-space-twounits);
    width: 100%;
    display: flex;
    max-width: var(--dl-size-size-maxwidth);
    align-items: flex-start;
    justify-content: center;
}

.home-pricing-card {
    gap: var(--dl-space-space-twounits);
    width: 100%;
    height: auto;
    display: flex;
    padding: var(--dl-space-space-threeunits);
    max-width: 450px;
    min-height: 450px;
    align-items: flex-start;
    border-radius: var(--dl-radius-radius-radius8);
    flex-direction: column;
    justify-content: space-between;
    background-color: var(--dl-color-primary1-blue60);
}

.pricingCardHeader {
    gap: var(--dl-space-space-oneandhalfunits);
    display: flex;
    align-items: flex-start;
    flex-direction: column;
    justify-content: flex-start;
}

.pricingCardName {
    font-family: "Raleway", serif;
    font-weight: bold;
    text-transform: uppercase;
}

.pricingCardDesc {
    line-height: 1.6;
}

.pricingPrice {
    display: flex;
    align-items: center;
    margin-bottom: var(--dl-space-space-twounits);
    flex-direction: row;
}

.pricingDollar {
    font-size: 1.15rem;
    margin-top: var(--dl-space-space-twounits);
    font-weight: 300;
}

.pricingNumber {
    font-size: 4rem;
    font-weight: 700;
}

.pricingBody {
    width: 100%;
    display: flex;
    align-items: flex-start;
    margin-bottom: var(--dl-space-space-unit);
    flex-direction: column;
}

.home-button {
    color: var(--dl-color-gray-black);
    width: 100%;
    margin-top: auto;
    transition: 0.3s;
    font-weight: 700;
    padding-top: 1rem;
    border-color: var(--dl-color-primary1-blue100);
    border-radius: 45px;
    padding-bottom: 1rem;
    background-color: transparent;
}

.home-button:hover {
    border-color: rgba(41, 20, 119, 0.9);
    background-color: rgba(217, 217, 217, 0.1);
}

.pricingMonth {
    font-size: 1.15rem;
    font-style: normal;
    margin-top: var(--dl-space-space-twounits);
    font-weight: 300;
}

.home-button1 {
    color: var(--dl-color-gray-white);
    width: 100%;
    margin-top: auto;
    transition: 0.3s;
    font-weight: 700;
    padding-top: 1rem;
    border-color: rgba(41, 20, 119, 0.9);
    border-width: 0;
    border-radius: 45px;
    padding-bottom: 1rem;
    background-color: var(--dl-color-primary1-blue100);
}

.home-button1:hover {
    border-color: rgba(41, 20, 119, 0.9);
    background-color: rgba(41, 20, 119, 0.9);
}

.home-button2 {
    color: var(--dl-color-gray-white);
    width: 100%;
    margin-top: auto;
    transition: 0.3s;
    font-weight: 700;
    padding-top: 1rem;
    border-color: #3d6e70ff;
    border-width: 0;
    border-radius: 45px;
    padding-bottom: 1rem;
    background-color: var(--dl-color-primary1-blue100);
}

.home-button2:hover {
    border-color: rgba(41, 20, 119, 0.9);
    background-color: rgba(41, 20, 119, 0.9);
}